.ai {
    padding: 50px 0;
    position: relative;
    height: 100vh;
}

.ai-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 1130px;
    width: 95%;
    margin: 0 auto;
}

.ai-image {
    margin-right: 40px;
}

.ai-image img {
    max-width: 500px;
    height: 400px;
    border-radius: 8px; /* Optional: Rounded corners for the image */
}

.separator {
    width: 100%; /* Adjust the separator width */
    border: none;
    border-top: 1px solid #fff;
    opacity: 0.5;
    margin: 20px 0;
}

.ai-description {
    color: white;
    max-width: 550px;
}

.ai-description h2 {
    font-size: 2.5rem; /* Adjust the font size as needed */
    margin-bottom: 20px;
}

.ai-description p {
    font-size: 1.125rem; /* Adjust the font size as needed */
    line-height: 1.6;
    margin-bottom: 30px;
}

@media only screen and (max-width:900px){
    .ai-content {
        text-align: center;
        flex-direction: column;
    }

    .ai-image {
        margin-bottom: 20px;
        margin-left: 40px;
    }

    .ai-image img {
        max-width: 400px;
        height: auto;
    }
}

@media only screen and (max-width:400px) {
    .ai-description h2 {
        font-size: 1.5rem;
    }

    .ai-description p {
        font-size: 1rem;
    }

    .ai-image img {
        display: none;
    }
}