main{
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(rgba(0, 0, 0, 0.80), rgba(0, 0, 0, 0.50)), url('./image/LRM_EXPORT_492151740525850_20190630_191622571.jpeg');
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    opacity: 0;
    transition: opacity 1s ease;
}

.fadeIn{
    opacity: 1;
}

.home-content{
    padding: 70px;
}

.home-content h2{
    background: linear-gradient(45deg, white, goldenrod);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 60px;
    letter-spacing: 3px;
}

.home-content p{
    color: rgba(255, 255, 255, 0.7);
    font-size: 30px;
    letter-spacing: 3px;
}

button{
    background-color: transparent;
    padding: 10px 10px;
    border: solid 1px rgba(255, 255, 255, 0.2);
    border-radius: 5px;
    font-size: 30px;
    margin: 10px;
    color: white;
    transition: background-position 0.4s ease;
    background-image: linear-gradient(to right, rgba(255, 255, 255, 0.2) 50%, transparent 50%);;
    background-size: 200% 100%;
    background-position: 100%;
    cursor: pointer;
}

button:hover{
    background-position: -100%;
}