.system {
    /* background-color: #333; */
    padding: 50px 0;
    position: relative; /* Menambahkan position relative */
    height: 100vh;
}

.system-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 1130px;
    width: 95%;
    margin: 0 auto;
}

.system-image {
    margin-left: 40px;
}

.system-image img {
    max-width: 500px;
    height: 400px;
    border-radius: 8px; /* Optional: Rounded corners for the image */
}

.separator {
    width: 100%; /* Adjust the separator width */
    border: none;
    border-top: 1px solid #fff;
    opacity: 0.5;
    margin: 20px 0;
}

.system-description {
    color: white;
    max-width: 550px;
}

.system-description h2 {
    font-size: 2.5rem; /* Adjust the font size as needed */
    margin-bottom: 20px;
}

.system-description p {
    font-size: 1.125rem; /* Adjust the font size as needed */
    line-height: 1.6;
    margin-bottom: 30px;
}

@media only screen and (max-width:900px) {
    .system-content {
        flex-direction: column;
        text-align: center;
    }

    .system-image {
        margin-right: 40px;
        margin-top: 20px;
    }

    .system-image img {
        width: 100%;
        height: auto;
    }
}

@media only screen and (max-width:400px) {
    .system-description h2 {
        font-size: 1.5rem;
    }

    .system-description p {
        font-size: 1rem;
    }

    .system-image img {
        display: none;
    }
}