.design-section{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding: 100px 0;
    scroll-behavior: smooth;
    overflow-y: hidden;
}
  
.design{
    display: flex;
    align-items: center;
    justify-content: center;
}
  
.timeline{
    width: 80%;
    height: auto;
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}
  
.timeline-content{
    padding: 20px;
    background: #34353a;
    -webkit-box-shadow: 5px 5px 10px #1a1a1a, -5px -5px 10px #242424;
            box-shadow: 5px 5px 10px #1a1a1a, -5px -5px 10px #242424;
    border-radius: 5px;
    color: white;
    padding: 1.75rem;
    transition: 0.4s ease;
    overflow-wrap: break-word !important;
    margin: 1rem;
    margin-bottom: 20px;
    border-radius: 6px;
}
  
.timeline-component{
    margin: 0px 20px 20px 20px;
}
  
@media screen and (min-width: 768px){
    .timeline{
      display: grid;
      grid-template-columns: 1fr 3px 1fr;
    }
    .timeline-middle{
      position: relative;
      background-image: linear-gradient(45deg, goldenrod, goldenrod, goldenrod);
      width: 3px;
      height: 100%;
    }
    .main-middle{
      opacity: 0;
    }
    .timeline-circle{
      position: absolute;
      top: 0;
      left: 50%;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      background-image: linear-gradient(45deg, goldenrod, goldenrod, goldenrod);
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
    }
}