.navbar{
  position: fixed;
  display: flex;
  align-items: center;
  padding: 20px;
  color: #fff;
  margin: 0 auto;
  z-index: 20;
}
  
.navbar a{
  color: #fff;
  text-decoration: none;
  margin-left: 20px;
  font-size: 20px;
}
  
.navbar a:hover{
  text-decoration: underline;
}
  
.navbar__left{
  margin-right: auto;
}
  
.navbar__right{
  display: flex;
  margin-left: auto;
}
  