.about{
    /* width: 100%; */
    min-height: 100vh;
    color: white;
}

.about-content{
    width: 1130px;
    max-width: 95%;
    margin: 0 auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.about-description{
    width: 550px;
    color: white;
}

.about-title h2{
    font-size: 50px;
}

.about-title hr{
    border: 1px solid goldenrod;
}

.about-description p{
    font-size: 18px;
    letter-spacing: 1px;
    line-height: 28px;
    margin-bottom: 45px;
}

.about-description button{
    text-decoration: none;
    padding: 13px 30px;
    color: goldenrod;
    border: goldenrod 1px solid;
    font-size: 25px;
    margin-left: 0;
}

@media only screen and (max-width:900px){
    .about-content{
        text-align: center;
        display: flex;
        flex-direction: column;
    }
    .about-title{
        margin-bottom: 45px;
    }
}

@media only screen and (max-width:600px){
    .about-title h2{
        font-size: 30px;
    }
    .about-description{
        width: 350px;
    }
    .about-description p{
        font-size: 20px;
    }
    .about-description button{
        font-size: 20px;
    }
}

@media only screen and (max-width:400px){
    .about-title h2{
        font-size: 20px;
    }
    .about-description{
        width: 200px;
    }
    .about-description p{
        font-size: 10px;
    }
    .about-description button{
        font-size: 10px;
    }
}